@import "./persianDatepicker-default.css";
@import "./fonts.css";
html,body{
    direction: rtl;
    font-family: "Vazir", -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}
html{
    overflow-x: hidden;
}

// tabs arrow
.fancy-layout .control-tabs.master-tabs:before, .fancy-layout.control-tabs.master-tabs:before {
    left: 8px;
    right: auto;
}

.fancy-layout .control-tabs.master-tabs:after, .fancy-layout.control-tabs.master-tabs:after {
    left: auto;
    right: 8px;
}


// select1
.select2-container--default {
    direction: rtl
}

.select2-container--open .select2-dropdown {
    left: 0;
    right: auto;
    direction: rtl
}


// date container
.datepicker-container {
    position: absolute;
    z-index: 100000;
    top: 0;
    direction: ltr
}

// code editor
.field-codeeditor .editor-toolbar {
    left: auto;
    right: 10px
}

// popover 
div.control-popover {
    right: auto!important
}

div.control-popover.placement-left>div::before {
    border-left: 9px solid rgba(0, 0, 0, .15);
    border-right: none;
    left: auto;
    right: -9px
}

div.control-popover.placement-left>div::after {
    border-left: 8px solid #fff;
    border-right: none;
    left: auto;
    right: -8px
}

div.control-popover.placement-left .popover-head::before {
    border-left: 8px solid #f9f9f9;
    border-right: none;
    left: auto;
    right: -8px
}

div.control-popover.placement-right>div::before {
    border-left: none;
    border-right: 9px solid rgba(0, 0, 0, .15);
    left: -9px;
    right: auto
}

div.control-popover.placement-right>div::after {
    border-left: none;
    border-right: 8px solid #fff;
    left: -8px;
    right: auto
}

div.control-popover.placement-right .popover-head::before {
    border-left: none;
    border-right: 8px solid #f9f9f9;
    left: -8px;
    right: auto
}

div.control-popover.placement-bottom>div::before {
    left: 14px;
    right: auto
}

div.control-popover.placement-bottom>div::after {
    left: 15px;
    right: auto
}

div.control-popover.placement-bottom .popover-head::before {
    left: 15px;
    right: auto
}

div.control-popover.placement-top>div::before {
    left: 14px;
    right: auto
}

div.control-popover.placement-top>div::after {
    left: 15px;
    right: auto
}


// cms: component list
.draggable-component-item>div::after,
.component-list .components div.layout-cell>div::after,
div.control-componentlist div.components div.layout-cell>div::after {
    text-indent: 3px!important;
    transform: rotate(180deg)!important
}


.component-list .components div.layout div.layout-row div.layout-cell > div:before{
    text-indent: 2px !important;
    transform: rotate(-90deg) !important;
}

//clock picker
.clockpicker-popover {
    right: auto
}

//plugin installer
.product-search > i.icon{
    left: auto !important;
    right: 15px !important;
}

// ?????
.sp-container{
    right: auto !important;
    left: 0;
}

// events log
.beautifier-message-container,
.beautifier-raw-content,
[data-input-preset-type="url"] {
    direction: ltr
}

.beautifier-container{
    direction: ltr;
    .beautifier-stacktrace-line-internal, .beautifier-stacktrace-line-function, .beautifier-file{
        margin-right: auto;
        margin-left: 55px;
    }
}


// main menu
nav#layout-mainmenu .toolbar-item:before {
    right: auto !important;
    left: -12px;
}
nav#layout-mainmenu .toolbar-item:after{
    left: auto !important;
    right: -12px;
}


